<template>
<div class="all">
  <section class="subtitle">


    <el-tabs type="border-card" v-model="tabsValue"  >
      <el-tab-pane label="Block info">There is currently  {{this.numberOfBlocks}} blocks
        <el-form :inline="true" class="demo-form-inline">

          <el-input
            placeholder="Enter a block number"
            v-model="blockNumber"
            clearable>
          </el-input>
          <el-form-item>
            <el-button type="primary" @click="AnalyseBlock">Analyse block</el-button>
            <el-button type="primary" @click="AnalyseBlockC">Analyse block2</el-button>
          </el-form-item>
        </el-form>
        <el-table
          :data="blockData"
          style="width: 100%">
          <el-table-column
            fixed
            prop="height"
            label="height"
            width="150">
          </el-table-column>
          <el-table-column
            prop="mediantime"
            label="mediantime"
            width="100">
          </el-table-column>
          <el-table-column
            prop="size"
            label="size"
            width="100">
          </el-table-column>
          <el-table-column
            prop="version"
            label="version"
            width="100">
          </el-table-column>
          <el-table-column
            prop="merkleroot"
            label="merkleroot"
            width="100">
          </el-table-column>
          <el-table-column
            prop="hash"
            label="hash"
            width="100">
          </el-table-column>
          <el-table-column
            prop="chainwork"
            label="chainwork"
            width="100">
          </el-table-column>
          <el-table-column
            prop="nonce"
            label="nonce"
            width="100">
          </el-table-column>
          <el-table-column
            prop="confirmations"
            label="confirmations"
            width="100">
          </el-table-column>
          <el-table-column
            prop="nextblockhash"
            label="nextblockhash"
            width="100">
          </el-table-column>
          <el-table-column
            prop="time"
            label="time"
            width="100">
          </el-table-column>
          <el-table-column
            prop="version"
            label="version"
            width="100">
          </el-table-column>
        </el-table>

      </el-tab-pane>
      <el-tab-pane label="Transaction">
        <el-form :inline="true" class="demo-form-inline">

          <el-input
            placeholder="Enter a transaction number"
            v-model="transactionNumber"
            clearable>
          </el-input>
          <el-form-item>
            <el-button type="primary" @click="Transaction">Get Transaction info</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="Role">Role</el-tab-pane>
      <el-tab-pane label="Task">Task</el-tab-pane>
    </el-tabs>
  </section>

<el-button>Default</el-button>



</div>
</template>
<script>

import axios from 'axios'

export default {
  name: "Homeview",
  mounted() {
    axios.get("https://bitcoin.mubiz.com/blocks")
    .then(response => {this.numberOfBlocks = response.data.blocks
    console.log( this.numberOfBlocks);})
  },
  data: () => ({
      tabsValue:"1",
      numberOfBlocks:"",
      blockNumber:"",
      transactionNumber:"",
      transactionData:"",
      getaddress:"https://blockchain.info/fr/rawaddr/",
      block_info:[],
      block_infoC:[],
      blockData:[],
      blockDataC:[],


    }),
  methods: {
    onSubmit() {
      console.log('submit!');
    },
    Transaction() {
      console.log("get transaction");
      axios.get("https://bitcoin.mubiz.com/transaction/"+this.transactionNumber+"/")
      .then(response => {this.transactionData = response.data;})
      console.log(this.transactionData);
    },
    AnalyseBlock() {
      console.log("it works");
      axios.get("https://bitcoin.mubiz.com/block/"+this.blockNumber+"/")
      .then(response => {this.block_info = response.data;})
        this.blockData=[{
          height:this.block_info.height,
          difficulty:this.block_info.difficulty,
          hash:this.block_info.hash,
          nonce:this.block_info.nonce,
          size:this.block_info.size,
          weight:this.block_info.weight,
          chainwork:this.block_info.whainwork,
          merkleroot:this.block_info.merkleroot,
          mediantime:this.block_info.mediantime,
          previousblockhash:this.block_info.previousblockhash,
          nextblockhash:this.block_info.nextblockhash,
          confirmation:this.block_info.confirmation,

        }]
      },
    AnalyseBlockC() {
      console.log("it works");
      axios.get("https://api.blockcypher.com/v1/btc/main/blocks/"+this.blockNumber)
      .then(response => {this.block_infoC = response.data;})
      .then(console.log(this.block_infoC))





    },
    GetAdressData(){
      console.log("get addressdata");
      axios.get("https://blockchain.info/fr/rawaddr/"+this.address)
      .then(response => {this.address_info = response.data;})
      console.log("get addressdata");


  }
  }

}
</script>
<style lang="stylus" scoped>
@import url('https://fonts.googleapis.com/css?family=Amaranth')
@import url('https://fonts.googleapis.com/css?family=Lato')
@import "../style/slick.css"

.visage1
  position:absolute
  top:10%


.image
  text-align: center

.subtitle
  padding:13vh 3vh 0 3vh
.phoneImage
  width:auto !important
  height :63vh

.subtexte
  padding:0 20vw 0 20vw

p
  font-family: 'Open sans', Regular
  font-size:2vh
  font-weight: 300
  text-shadow:  5px 5px 5px #FECFFD,1px 1px 1px #FFF198



#Homeview
  font-family: 'Open sans', Regular
  font-weight: 300
  font-style: normal
  font-size:2.8vh
  font-color:black
</style>



